  src: url("../assets/fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../assets/fonts/Montserrat-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Stapel";
  src: url("../assets/fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../assets/fonts/Stapel-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --max-container-width: 181rem;
  --content-padding: 5.5rem;
  --primary-color: #148eff;
  --primary-color-hover: #1777cf;
  --montserrat: "Montserrat", sans-serif;
  --secondary-color: #ed5835;
  --secondary-color-hover: #d74928;
  --text-color: #011f3a;
}
@media only screen and (max-width: 640px) {
  :root {
    --content-padding: 1.5rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  font-size: clamp(4.2px, 0.52vw, 25px);
  -webkit-tap-highlight-color: transparent;
}
@media screen and (min-width: 1921px) {
  html {
    font-size: 9px;
  }
}
@media only screen and (max-width: 640px) {
  html {
    font-size: 10px;
  }
}

body {
  font-size: 1.6rem;
  line-height: 1.2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: var(--montserrat);
  font-weight: 400;
  color: var(--text-color);
  overflow-y: scroll;
}

main {
  display: block;
}

ul,
ol {
  list-style: none;
}

ol {
  counter-reset: ordered-list;
}
ol > li {
  counter-increment: ordered-list;
}
ol > li::before {
  content: counter(ordered-list, decimal-leading-zero) ".";
}

input,
textarea {
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  box-shadow: none;
  border-radius: 0;
  width: 100%;
  background-color: transparent;
  display: block;
}

input:not([type=checkbox]):not([type=radio]) {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  box-shadow: none;
  display: block;
  width: 100%;
  overflow: auto;
  height: 20rem;
}
textarea::-webkit-scrollbar {
  background-color: white;
  width: 0.6rem;
}
textarea::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
}
textarea::-webkit-scrollbar-button {
  display: none;
}
textarea::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  -webkit-appearance: none;
  font: inherit;
  color: inherit;
  outline: none;
  display: inline-flex;
  position: relative;
  transition: color 0.2s;
}

a {
  background-color: transparent;
  text-decoration: none;
  transition: color 0.2s;
}
a:link, a:visited {
  color: inherit;
}

a:where(:not[class]):link, a:where(:not[class]):visited {
  color: var(--primary-color);
}

svg {
  flex-shrink: 0;
  fill: currentColor;
  transition: fill 0.2s;
}

img {
  border-style: none;
  max-width: 100%;
  display: block;
}

img[data-sizes=auto] {
  display: block;
  width: 100%;
}

img.lazyload:not([src]) {
  visibility: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

th {
  font-weight: 400;
}

td,
th {
  text-align: left;
  vertical-align: top;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.parsley-errors-list.filled {
  width: 100%;
  font-size: 1.4rem;
  color: var(--primary-color);
  order: 10;
  margin-top: 1rem;
  font-weight: 600;
}
@media only screen and (max-width: 640px) {
  .parsley-errors-list.filled {
    font-size: 1.2rem;
    margin-top: 0.5rem;
  }
}

.no-scroll {
  overflow-y: scroll;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.no-scroll body {
  height: auto;
  overflow: hidden;
}

@media only screen and (max-width: 640px) {
  .hide-on-mobile {
    display: none;
  }
}

::selection {
  color: white;
  background-color: var(--primary-color);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: transparent;
}

@media only screen and (max-width: 640px) {
  .hide-on-mobile {
    display: none;
  }
}

@media only screen and (min-width: 641px) {
  .show-on-mobile {
    display: none;
  }
}

.container {
  width: 100%;
  max-width: calc(var(--max-container-width) + 2 * var(--content-padding));
  margin: 0 auto;
  padding: 0 var(--content-padding);
}
@supports (width: max(20vw, 100px)) {
  .container {
    padding-left: max(env(safe-area-inset-left, 1.4rem), var(--content-padding));
    padding-right: max(env(safe-area-inset-right, 1.4rem), var(--content-padding));
  }
}

.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.blue-small-btn {
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  align-self: flex-start;
  line-height: 100%;
  text-transform: uppercase;
  display: inline-block;
  padding: 2rem 3rem;
  background-color: var(--primary-color);
  color: white;
  transition: background-color 0.2s, color 0.2s;
  border-radius: 1.5rem;
  flex-shrink: 0;
  white-space: nowrap;
  justify-content: center;
  text-align: center;
}
@media only screen and (max-width: 640px) {
  .blue-small-btn {
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
    padding: 1.5rem 2rem;
    border-radius: 1rem;
  }
}
.blue-small-btn:link, .blue-small-btn:visited {
  color: white;
}
.no-touch .blue-small-btn:hover, .touch .blue-small-btn:active {
  background-color: var(--primary-color-hover);
}
.blue-small-btn--orange {
  background-color: var(--secondary-color);
}
.no-touch .blue-small-btn--orange:hover, .touch .blue-small-btn--orange:active {
  background-color: var(--secondary-color-hover);
}

.blue-btn {
  font-style: normal;
  font-weight: 600;
  font-size: 2rem;
  line-height: 100%;
  align-self: flex-start;
  line-height: 100%;
  text-transform: uppercase;
  display: inline-block;
  padding: 2.5rem 5rem;
  background-color: var(--primary-color);
  color: white;
  transition: background-color 0.2s, color 0.2s;
  border-radius: 2rem;
  flex-shrink: 0;
  white-space: nowrap;
  justify-content: center;
  text-align: center;
}
@media only screen and (max-width: 640px) {
  .blue-btn {
    padding: 2rem 4rem;
    border-radius: 1.5rem;
    font-style: normal;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
  }
}
.blue-btn:link, .blue-btn:visited {
  color: white;
}
.no-touch .blue-btn:hover, .touch .blue-btn:active {
  background-color: var(--primary-color-hover);
}
.blue-btn--orange {
  background-color: var(--secondary-color);
}
.no-touch .blue-btn--orange:hover, .touch .blue-btn--orange:active {
  background-color: var(--secondary-color-hover);
}

.arrow-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 0.2rem solid var(--primary-color);
  padding: 2.3rem 5rem;
  font-style: normal;
  font-weight: 600;
  font-size: 2rem;
  line-height: 100%;
  text-transform: uppercase;
  border-radius: 2rem;
  flex-shrink: 0;
  white-space: nowrap;
  align-self: flex-start;
  transition: background-color 0.2s, color 0.2s;
  color: var(--text-color);
}
.arrow-btn:link, .arrow-btn:visited {
  color: var(--text-color);
}
.arrow-btn svg {
  width: 2rem;
  height: 2rem;
  flex-shrink: 0;
  margin-left: 2rem;
  fill: currentColor;
}
.no-touch .arrow-btn:hover, .touch .arrow-btn:active {
  color: var(--primary-color);
}
.arrow-btn--white {
  border-color: white;
  color: white;
}
.arrow-btn--white:link, .arrow-btn--white:visited {
  color: white;
}
.no-touch .arrow-btn--white:hover, .touch .arrow-btn--white:active {
  color: white;
}
@media only screen and (max-width: 640px) {
  .arrow-btn {
    padding: 1.8rem 4rem;
    border-radius: 1.5rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
  }
  .arrow-btn svg {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 1.5rem;
  }
}

.arrow-btn-small {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 0.2rem solid var(--primary-color);
  padding: 1.7rem 3rem;
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 100%;
  text-transform: uppercase;
  border-radius: 1.5rem;
  flex-shrink: 0;
  white-space: nowrap;
  align-self: flex-start;
  transition: background-color 0.2s, color 0.2s;
  color: var(--text-color);
}
.arrow-btn-small:link, .arrow-btn-small:visited {
  color: var(--text-color);
}
.arrow-btn-small svg {
  width: 1.7rem;
  height: 1.7rem;
  flex-shrink: 0;
  margin-left: 1rem;
  fill: currentColor;
}
.no-touch .arrow-btn-small:hover, .touch .arrow-btn-small:active {
  color: var(--primary-color);
}
.arrow-btn-small--filled {
  background-color: var(--primary-color);
  color: white;
}
.no-touch .arrow-btn-small--filled:hover, .touch .arrow-btn-small--filled:active {
  color: white;
  background-color: var(--primary-color-hover);
}
.arrow-btn-small--green {
  color: white;
  background: #4fcd3b;
  border-color: #4fcd3b;
}
.arrow-btn-small--green:link, .arrow-btn-small--green:visited {
  color: white;
}
.no-touch .arrow-btn-small--green:hover, .touch .arrow-btn-small--green:active {
  background: #4fcd3b;
}
.arrow-btn-small--white {
  border-color: white;
  color: white;
}
.arrow-btn-small--white:link, .arrow-btn-small--white:visited {
  color: white;
}
.no-touch .arrow-btn-small--white:hover, .touch .arrow-btn-small--white:active {
  color: white;
}
@media only screen and (max-width: 640px) {
  .arrow-btn-small {
    padding: 1.3rem 2rem;
    border-radius: 1.5rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
    border-radius: 1rem;
  }
  .arrow-btn-small svg {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 1rem;
  }
}

.arrow-btn-filled {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 5rem;
  border-radius: 2rem;
  color: white;
  background-color: var(--primary-color);
  transition: background-color 0.2s, color 0.2s;
  font-style: normal;
  font-weight: 600;
  font-size: 2rem;
  line-height: 100%;
  text-transform: uppercase;
}
.arrow-btn-filled svg {
  width: 2rem;
  height: 2rem;
  flex-shrink: 0;
  margin-left: 2rem;
  fill: currentColor;
}
.arrow-btn-filled:link, .arrow-btn-filled:visited {
  color: white;
}
.no-touch .arrow-btn-filled:hover, .touch .arrow-btn-filled:active {
  background-color: var(--primary-color-hover);
}
@media only screen and (max-width: 640px) {
  .arrow-btn-filled {
    padding: 2rem 4rem;
    border-radius: 1.5rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
  }
  .arrow-btn-filled svg {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 1.5rem;
  }
}

.small-arrow-btn-filled {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 5rem;
  font-style: normal;
  font-weight: 600;
  font-size: 2rem;
  line-height: 100%;
  text-transform: uppercase;
  color: white;
  background-color: var(--primary-color);
  border-radius: 2rem;
  align-self: flex-start;
  transition: color 0.2s, background-color 0.2s;
}
.small-arrow-btn-filled:link, .small-arrow-btn-filled:visited {
  color: white;
}
.no-touch .small-arrow-btn-filled:hover, .touch .small-arrow-btn-filled:active {
  background-color: var(--primary-color-hover);
}
.small-arrow-btn-filled--orange {
  background-color: var(--secondary-color);
}
.no-touch .small-arrow-btn-filled--orange:hover, .touch .small-arrow-btn-filled--orange:active {
  background-color: var(--secondary-color-hover);
}
.small-arrow-btn-filled--green {
  background: #4fcd3b;
}
.no-touch .small-arrow-btn-filled--green:hover, .touch .small-arrow-btn-filled--green:active {
  background: #4fcd3b;
}
.small-arrow-btn-filled svg {
  flex-shrink: 0;
  width: 2rem;
  height: 2rem;
  margin-left: 2rem;
  fill: currentColor;
}
@media only screen and (max-width: 640px) {
  .small-arrow-btn-filled {
    padding: 2rem 4rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
    border-radius: 1.5rem;
  }
  .small-arrow-btn-filled svg {
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 1.5rem;
  }
}

.outline-btn {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: flex-start;
  border: 0.2rem solid #148eff;
  border-radius: 1.5rem;
  padding: 1.7rem 3rem;
  flex-shrink: 0;
  color: transparent;
  background-color: #fff;
}
.outline-btn--large {
  padding: 2.5rem;
}
.outline-btn:link, .outline-btn:visited {
  color: transparent;
}
@media only screen and (max-width: 640px) {
  .outline-btn {
    padding: 1.3rem 2.5rem;
    border-radius: 1rem;
  }
  .outline-btn--large {
    padding: 1.7rem;
  }
}
.outline-btn svg {
  width: 1.7rem;
  height: 1.7rem;
  fill: var(--text-color);
  transition: fill 0.2s;
}
@media only screen and (max-width: 640px) {
  .outline-btn svg {
    width: 1.5rem;
    height: 1.5rem;
  }
}
.no-touch .outline-btn:hover svg, .touch .outline-btn:active svg {
  fill: var(--primary-color);
}
.outline-btn:active, .outline-btn.active {
  color: var(--primary-color);
}
.outline-btn:active svg, .outline-btn.active svg {
  fill: var(--primary-color);
}

.breadcrumbs {
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 1.2;
  color: #aab9c7;
}
.breadcrumbs a {
  text-decoration: none;
}
.breadcrumbs a:link, .breadcrumbs a:visited {
  color: #aab9c7;
}
.breadcrumbs a > span {
  background-image: linear-gradient(#aab9c7, #aab9c7);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
  transition: background-size 0.25s ease-out;
}
.no-touch .breadcrumbs a:hover > span, .touch .breadcrumbs a:active > span {
  background-size: 100% 1px;
}
.breadcrumbs a.active {
  color: #495f73;
  pointer-events: none;
}

.slider-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.slider-pagination .swiper-pagination-bullet {
  opacity: 1;
  margin: 0;
  background: #BED2E4;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 1rem;
  transition: background-color 0.2s, width 0.2s;
  flex-shrink: 0;
}
.slider-pagination .swiper-pagination-bullet-active {
  width: 3rem;
  background-color: var(--primary-color);
}

.secondary-heading {
  font-style: normal;
  font-weight: 600;
  font-size: 6.5rem;
  line-height: 100%;
}
@media only screen and (max-width: 640px) {
  .secondary-heading {
    font-weight: 600;
    font-size: 3rem;
    line-height: 100%;
  }
}

.small-heading {
  font-style: normal;
  font-weight: 600;
  font-size: 2rem;
  line-height: 125%;
  color: #495f73;
}
@media only screen and (max-width: 640px) {
  .small-heading {
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
  }
}

.text-input {
  height: 7rem;
  border: 2px solid #bed2e4;
  border-radius: 2rem;
  padding: 0 2.5rem;
  font-weight: 600;
  font-size: 2rem;
  line-height: 100%;
  transition: color 0.2s, border-color 0.2s;
  color: #011f3a;
  width: 100%;
}
.text-input::-webkit-input-placeholder {
  color: #bed2e4;
}
.text-input:-moz-placeholder {
  color: #bed2e4;
}
.text-input::-moz-placeholder {
  color: #bed2e4;
}
.text-input:-ms-input-placeholder {
  color: #bed2e4;
}
.text-input--small {
  height: 5.5rem;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 100%;
  padding: 0 2rem;
  border-radius: 1.5rem;
}
.text-input:hover {
  border-color: #495f73;
}
.text-input:focus {
  border-color: var(--primary-color);
  color: #011f3a;
}
.text-input.parsley-error {
  border-color: var(--primary-color);
}
.text-input--blue {
  border-color: var(--primary-color);
  color: white;
}
.text-input--blue:hover {
  border-color: var(--primary-color);
}
.text-input--blue:focus {
  border-color: var(--primary-color);
  color: white;
}
.text-input--blue::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--blue:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--blue::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--blue:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--white {
  border-color: rgba(255, 255, 255, 0.5);
  color: white;
}
.text-input--white::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--white:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--white::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--white:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.text-input--white:hover {
  border-color: white;
}
.text-input--white:focus {
  border-color: var(--primary-color);
  color: white;
}
@media only screen and (max-width: 640px) {
  .text-input {
    height: 5rem;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 100%;
    padding: 0 2rem;
    border-radius: 1.5rem;
    border-width: 1.5px;
  }
}

textarea.text-input {
  height: 14rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.page-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  z-index: 200;
  transition: background-color 0.2s, box-shadow 0.2s;
}
.modal-open .page-header {
  padding-right: var(--sb-width, 0);
}
.page-header.fixed {
  background-color: #fff;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
}

.page-header__row {
  padding: 3rem 0;
  display: flex;
  align-items: center;
  transition: padding 0.2s;
  transition: transform 0.6s ease-out 1.2s, opacity 0.6s ease 1.2s, visibility 0.6s ease 1.2s, padding 0.2s;
}
body:not(.animatable):not(.no-animated-header) .page-header__row {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-100%);
}
.page-header.fixed .page-header__row {
  padding: 1.7rem 0;
